import React, { useState, useEffect } from 'react';
import { Select, Input, Button, message, Popconfirm } from 'antd';
import { PlusOutlined, DeleteOutlined } from '@ant-design/icons';
import { collection, addDoc, getDocs, deleteDoc, doc } from 'firebase/firestore';
import { db } from '../../config';
import { toast } from 'react-toastify';

const { Option } = Select;

const Themphongban = ({ value, onChange }) => {
    const [departments, setDepartments] = useState([]);
    const [newDepartment, setNewDepartment] = useState('');
    const [isAdding, setIsAdding] = useState(false);
    const [isOpen, setIsOpen] = useState(false);

    useEffect(() => {
        fetchDepartments();
    }, []);

    const fetchDepartments = async () => {
        try {
            const querySnapshot = await getDocs(collection(db, 'ListPhongban'));
            const departmentList = querySnapshot.docs.map(doc => ({ id: doc.id, name: doc.data().name }));
            setDepartments(departmentList);
        } catch (error) {
            console.error("Error fetching departments:", error);
            message.error("Failed to fetch departments.");
        }
    };

    const addDepartment = async () => {
        if (newDepartment.trim()) {
            try {
                await addDoc(collection(db, 'ListPhongban'), { name: newDepartment.trim() });
                toast.success('Thêm phòng ban thành công.', {
                    position: "top-right",
                    autoClose: 3000,
                    hideProgressBar: false,
                    closeOnClick: true,
                    pauseOnHover: true,
                    draggable: true,
                    progress: undefined,
                });
                setNewDepartment('');
                setIsAdding(false);
                fetchDepartments();
            } catch (error) {
                console.error("Error adding department:", error);
            }
        }
    };

    const deleteDepartment = async (departmentId) => {
        try {
            await deleteDoc(doc(db, 'ListPhongban', departmentId));
            toast.success('Xóa phòng ban thành công.', {
                position: "top-right",
                autoClose: 3000,
                hideProgressBar: false,
                closeOnClick: true,
                pauseOnHover: true,
                draggable: true,
                progress: undefined,
            });
            fetchDepartments();
        } catch (error) {
            console.error("Error deleting department:", error);
        }
    };

    const handleChange = (value) => {
        if (value === 'add_new') {
            setIsAdding(true);
        } else {
            const selectedDept = departments.find(dept => dept.id === value);
            onChange(selectedDept ? selectedDept.name : value);
        }
    };

    return (
        <div>
            <Select
                style={{ width: '100%' }}
                placeholder="Chọn phòng ban"
                value={value}
                onChange={handleChange}
                onDropdownVisibleChange={(open) => setIsOpen(open)}
                dropdownRender={menu => (
                    <div>
                        {menu}
                        {isAdding ? (
                            <div style={{ display: 'flex', flexWrap: 'nowrap', padding: 8 }}>
                                <Input
                                    style={{ flex: 'auto' }}
                                    value={newDepartment}
                                    onChange={e => setNewDepartment(e.target.value)}
                                    placeholder="Nhập tên phòng ban mới"
                                />
                                <Button
                                    type="link"
                                    icon={<PlusOutlined />}
                                    onClick={addDepartment}
                                    style={{ whiteSpace: 'nowrap' }}
                                >
                                    Thêm phòng ban
                                </Button>
                                <Button
                                    type="link"
                                    onClick={() => setIsAdding(false)}
                                    style={{ whiteSpace: 'nowrap', marginLeft: 8 }}
                                >
                                    Hủy
                                </Button>
                            </div>
                        ) : (
                            <Button
                                type="link"
                                onClick={() => setIsAdding(true)}
                                style={{ whiteSpace: 'nowrap', margin: 8 }}
                            >
                                Thêm phòng ban mới
                            </Button>
                        )}
                    </div>
                )}
            >
                {departments.map(dept => (
                    <Option key={dept.id} value={dept.id}>
                        {dept.name}
                        {isOpen && (
                            <Popconfirm
                                title="Bạn có chắc chắn muốn xóa phòng ban này?"
                                onConfirm={() => deleteDepartment(dept.id)}
                                okText="Có"
                                cancelText="Không"
                            >
                                <DeleteOutlined
                                    onClick={(e) => e.stopPropagation()}
                                    style={{ marginLeft: 8 }}
                                />
                            </Popconfirm>
                        )}
                    </Option>
                ))}
            </Select>
        </div>
    );
};

export default Themphongban;